.ChocolateBox {
  display: inline-flex;
  flex-direction: column;
  vertical-align: middle;
  width : 70%;
  justify-content: center;
  padding: 10px 40px 20px;
  text-align: center;
  margin: 30px 0px;
}

.ChocolateBox h2 {
  margin: 0px;
}

.ChocolateBox h3 {
  margin-top: 10px;
  margin-bottom: 0px;
}

.div-chocobox {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
  width: 60%;
  max-width: 500px;
  height: 300px;
  border: 10px solid rgb(78, 53, 39);
  border-radius: 22px;
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: rgb(90, 64, 50);
}

.box-square {
  width: 22%;
  height: auto;
  border-radius: 10px;
  border: 3px solid rgb(78, 55, 40);;
  margin : 3px;
}

.box-square.Black {
  background-color : #2a1b0e ;
}

.box-square.Milk {
  background-color : #825a39 ;
}

.box-square.Pistachio {
  background-color : #94ac54 ;
}

.box-square.Raspberry {
  background-color : #ce5871 ;
}

button.eat-btn {
  cursor: pointer;
  padding: 10px 23px;
  border-radius: 5px;
  border: 2px solid rgb(25, 25, 25, 0.1);
  margin: 20px auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-weight: bold;
  font-family: inherit;
}

.Chocolate {
  display: block;
  margin: 20px auto;
}

button.choco-btn {
  display: block;
  margin : 10px auto;
  padding : 5px 8px;
  border-radius: 15px;
  border : 1px solid grey;
  cursor: pointer;
}
section.ChocolateList {
  display: inline-block;
  background-color: lightgrey;
  padding: 20px;
  height: inherit;
}
.list-content {
  text-align: center
}

.list-content ul {
  padding: 0px
}
.App {
  border : 3px solid lightgray;
  border-radius: 10px;
  display: flex;
  vertical-align: middle;
  margin: 30px auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  justify-content: left;
  padding: 0;
}


body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

